.neumorphism{
    border-radius: 50px;
    background: linear-gradient(145deg, #4da7db, #5bc6ff);
    box-shadow:  20px 20px 60px #489dcf, 
                -20px -20px 60px #62d5ff;
}
.bgblue{
    background: #55b9f3;
}

.stack{
    z-index: 100;
}